.image-next,
.image-prev {
  position: relative;
  width: 100%;
  height: 100%;
}

.image-prev:hover div {
  background: radial-gradient(
    96.33% 47.45% at 96.33% 50.14%,
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
  -webkit-filter: blur(4px);
          filter: blur(4px);
  z-index: 10;
  cursor: pointer;
}

.image-next:hover div {
  background: radial-gradient(
    96.33% 47.45% at 96.33% 50.14%,
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  -webkit-filter: blur(4px);
          filter: blur(4px);
  z-index: 10;
  cursor: pointer;
}

.image-next ion-icon,
.image-prev ion-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.item-tool {
  display: flex;
  flex-direction: column;
  width: 13em;
  height: 13em;
